import React, { Component } from 'react'
import dataArticles from '../dataArticles'
import Article from './Article'
import CreatingArticle from './CreatingArticle'

export default class Articles extends Component {
  state = {
    artilcesArr: [],
    showCreateArticleInterface: true,
    CurrentArticleEdit: {}
  }

  componentDidMount = () => this.getArticles()

  getArticles() {
    if (!localStorage.getItem('arrArticles')) {
      localStorage.setItem('arrArticles', JSON.stringify([...dataArticles]))
      this.setState({ artilcesArr: [...dataArticles] });
    }
    else this.setState({ artilcesArr: JSON.parse(localStorage.getItem('arrArticles')) })
  }

  removeComment = (idArticle, idComment) => {
    let arr = [...this.state.artilcesArr];
    arr[idArticle].comments.splice(idComment, 1)
    this.setState({ artilcesArr: arr });
    localStorage.setItem('arrArticles', JSON.stringify(arr))
  }

  addComment = (i, name, commentT) => {
    if (name === '') name = 'anonymous'
    let arr = [...this.state.artilcesArr];
    arr[i].comments.push({
      author: name,
      commentText: commentT,
    })
    this.setState({ artilcesArr: arr });
    localStorage.setItem('arrArticles', JSON.stringify(arr))
  }

  showHideCreateArticlesInterface = () => {
    this.setState({ showCreateArticleInterface: !this.state.showCreateArticleInterface })
  }

  addNewArticle = (tt, desc, tx) => {
    let arr = [...this.state.artilcesArr]
    arr.push({
      title: tt,
      description: desc,
      text: tx,
      comments: [],
    })
    this.setState({ artilcesArr: arr });
    localStorage.setItem('arrArticles', JSON.stringify(arr))
    this.showHideCreateArticlesInterface()
  }

  removeArticle = id => {
    let arr = [...this.state.artilcesArr]
    arr.splice(id, 1)
    this.setState({ artilcesArr: arr });
    localStorage.setItem('arrArticles', JSON.stringify(arr))
  }

  editArticle = i => this.setState({ CurrentArticleEdit: [...this.state.artilcesArr][i] })

  applyEditArticle = (i, tt, ds, tx) => {
    let arr = [...this.state.artilcesArr]
    arr[i].title = tt
    arr[i].description = ds
    arr[i].text = tx
    this.setState({ artilcesArr: arr });
    localStorage.setItem('arrArticles', JSON.stringify(arr))
  }

  render() {

    return (
      <div className="container">
        <div className="row">
          {this.state.artilcesArr.map((it, i) => {
            return <Article
              i={i}
              key={i}
              it={it}
              removeComment={this.removeComment}
              addComment={this.addComment}
              removeArticle={this.removeArticle}
              editArticle={this.editArticle}
              CurrentArticleEdit={this.state.CurrentArticleEdit}
              applyEditArticle={this.applyEditArticle}
            />
          })}

          <div className="col">

            <div className="new-article">

              {this.state.showCreateArticleInterface ?
                <button
                  className='new-article-create-btn'
                  onClick={() => this.showHideCreateArticlesInterface()}>
                  create new article
                </button>
                :
                <CreatingArticle
                  addNewArticle={this.addNewArticle}
                />
              }

            </div>
          </div>
        </div>
      </div >
    )
  }
}
