import React, { Component } from 'react'

export default class CreatingArticle extends Component {
  state = {
    title: '',
    description: '',
    text: '',
    titleCls: ['create-article-input-title'],
    descriptionCls: ['create-article-input-description'],
    textCls: ['create-article-input-text'],
  }

  updateTitle = e => this.setState({ title: e.target.value });
  updateDescription = e => this.setState({ description: e.target.value });
  updateText = e => this.setState({ text: e.target.value });

  handleAddButton = (tt, ds, tx) => {
    this.setState({
      titleCls: ['create-article-input-title'],
      descriptionCls: ['create-article-input-description'],
      textCls: ['create-article-input-text'],
    });

    if (tt === '') {
      this.setState({ titleCls: ['create-article-input-title', 'input-red'] })
      return
    }
    if (ds === '') {
      this.setState({ descriptionCls: ['create-article-input-description', 'input-red'] })
      return
    }
    if (tx === '') {
      this.setState({ textCls: ['create-article-input-text', 'input-red'] })
      return
    }

    this.props.addNewArticle(tt, ds, tx)
    this.setState({
      title: '',
      description: '',
      text: '',
      titleCls: ['create-article-input-title'],
      descriptionCls: ['create-article-input-description'],
      textCls: ['create-article-input-text'],
    });
  }


  render() {
    return (
      <div>
        <div className='new-article-create'>

          <div className="create-article-title-main">Creating new article</div>

          <h3 className="create-article-title">Title</h3>
          <textarea
            value={this.state.title}
            onChange={(e) => this.updateTitle(e)}
            className={this.state.titleCls.join('  ')}
            type="text" />

          <h3 className="create-article-title">Description</h3>
          <textarea
            value={this.state.description}
            onChange={(e) => this.updateDescription(e)}
            className={this.state.descriptionCls.join(' ')}
            type="text" />

          <h3 className="create-article-title">Text</h3>
          <textarea
            value={this.state.text}
            onChange={(e) => this.updateText(e)}
            className={this.state.textCls.join(' ')}
            type="text" />

          <button
            onClick={() => this.handleAddButton(
              this.state.title, this.state.description, this.state.text
            )}
            className="create-article-button">
            Apply
        </button>
        </div>

      </div >
    )
  }
}
